/*CSS Reset from http://meyerweb.com/eric/tools/css/reset/*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
	box-sizing : border-box;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
	height : 100%;
}

/* Body typeface and color */
body {
	font-family : "Nunito", sans-serif;
	background-color : #f8f8f8;
	height : 100%;
}

/* Remove default styling for links */
a, a:active, a:visited {
	text-decoration : none;
}

/* Navigation */
.topnav {
	position : fixed;
	top: 0;
	left : 0;
	background-color: #ffffff;
	width : 100%;
	height : 50px;
}

.topnav__list {
	padding : 20px;
	display : flex;
	justify-content: space-between;
}

.topnav__sublist {
	width : 40%;
	display : flex;
	justify-content : space-around;
}

.topnav__link, .topnav__sublist__link {
	color : #919191;
}

.topnav__sublist__link {
	padding : .95em .9em .9em .9em;
}

/* Add purple border on top for active menu */
.topnav__sublist__link:hover {
	border-top : 3px solid #6C3483;
	color : #6d6d6d;
}

/*Container div for sidebar and wrapper*/
.reviews-sidebar-wrapper {
	margin-top : 3em;
	display : flex;
	height : 100%;
}

/*Reviews section*/
.reviews {
	width : 75%;
}

/* Review Card */

.review__card:first-child {
	margin-top: 4em;
}

.review__card {
	background-color : #ffffff;
	margin: 3.5em 3.5em 3.5em 5em;
	display : flex;
	border-radius : 5px;
	min-height : 40%;
}

.review__card__contentBox {
	width : 75%;
	display : flex;
	flex-direction : column;
}

.review__card__text {
	padding : 2em 1.5em 2em 4.5em;
	flex : 2;
}

.review__heading {
	font-family : "Nunito";
	font-size : 0.9rem;
	margin-bottom : 1em;
}

.review__textContainer {
	line-height : 1.4;
	font-size : 0.8rem;
	color : #919191;
	margin-bottom : .6em;
}

.readMoreButton {
	font-size : .8rem;
	margin-top : 1em;
}

/*Review Tags*/
.review__card__taglist {
	display : flex;
	flex-wrap: wrap;
	list-style: none;
	justify-content: flex-start;
	align-items: center;
	height : 100%;
}

.review__card__tagItem, .review__card__tagLink,
.sidebar__tagItem, .sidebar__tagLink {
	height : 25px;
	line-height : 25px;
}

.review__card__tagItem:first-child {
	margin-left : 1em;
}

.review__card__tagItem {
	margin : .5em .5em .5em .5em;
}


.review__card__tagLink, .sidebar__tagLink {
	padding : .3em .6em .3em 1.6em;
	background-color : #f8f8f8;
	position: relative;
	color : #919191;;
}

/* Circle shape before tags*/
.review__card__tagLink:before, .sidebar__tagLink:before {
	content : "";
	position : absolute;
	top : .8em;
	left : .8em;
	width: 4px;
	height : 4px;
	background-color : #919191;
	border-radius : 2px;
}


.review__card__footer {
	font-size : 0.8rem;
	border-top: 1px solid #ededed;
	flex : 1;
}

.review__card__bookCover {
	width : 25%;
	background-color : #F36A6F;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	display : flex;
	justify-content: center;
	align-items : center;
}

.review__card__bookCoverImage {
	width : 65%;
}

/* Sidebar */
.sidebar {
	width : 25%;
	height : 100%;
	background-color : #ffffff;
	display : flex;
	flex-direction : column;
	justify-content: flex-start;
	padding-top: 4em;
	z-index : -1;
}

.sidebar__tags {
	margin-bottom: 1.5em;
}

.sidebar__heading__tags, .sidebar__heading__recentlyReviewed {
	font-family : "Nunito";
	font-size : 1rem;
	text-align: center;
	margin-bottom : 1.3em;
}

/* Line for */
.sidebar__horizontalLine {
	height : 1px;
	width : 20%;
	border-bottom : 3px solid #6C3483;
	text-align : center;
	margin : 0 auto;
}

.sidebar__tagLink {
	z-index : -2;
	font-size : .8rem;
}

.sidebar__taglist {
	padding : 1em 1em;
	display : flex;
	flex-direction: column;
	flex-wrap : wrap;
	align-items : center;
}

.sidebar__tagItem {
	margin : .3em .3em;
}

.sidebar__recentlyReviewed {
	padding : .8em;
}



/* article.html Styles */

.article__container {
	margin-top: 5em;
	padding : 2em 14em;
	line-height: 1.5;
}

.article__title {
	font-size : 2rem;
	font-weight : 800;
	text-align: center;
	margin-bottom : .5em;
}

.article__publishedDate {
	font-size : 1.2rem;
	text-align : right;
	margin-bottom : 1em;
}

.article__text > p, .article__text blockquote {
	margin-bottom : 1em;
}

.article__text > h2, .article__text > h3  {
	font-weight : 800;
	font-size : 1.5rem;
	margin-bottom : .5em;
}

.article__text strong {
	font-weight : bold;
}

.article__text blockquote {
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: 1.5em 1em;
  padding: 0.5em 1em;
  quotes: "\201C""\201D""\2018""\2019";
}
.article__text blockquote:before {
  color: #ccc;
  content: "";
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}

.article__text blockquote p {
  display: inline;
}


/* Media Queries for Responsive Views */

@media (max-width : 960px) {

	.article__container {
		padding : 2em 10em;
		line-height : 1.6;
	}

	.reviews {
		width : 80%;
	}

	.sidebar {
		width : 20%;
	}

	.review__card {
		margin: 3.5em 3em 0em 3em;
	}
}

@media (max-width: 800px) {

	.article__container {
		padding : 2em 8em;
		line-height : 1.6;
	}

	.reviews {
		width: 100%;
		padding : 1em 2em;
	}

	.sidebar {
		display: none;
	}

	.review__card:first-child {
		margin-top : 3em;
	}

	.review__card {
		width : 100%;
		margin: 3.5em auto;
	}

	.review__card__contentBox {
		width: 75%;
	}

	.review__card__bookCover {
		width : 25%;
	}

	.review__heading {
		font-size : 1.2rem;
	}

	.review__textContainer {
		font-size : .9rem;
	}

	.readMoreButton {
		padding : 1.2em 0em;
		font-size: .9rem;
	}

	.review__card__footer {
		font-size: .9rem;
		padding : .2em;
	}

}

/* Two-column layout */
@media (max-width : 650px) {
	.article__container {
		padding : 2em 6em;
	}


	.sidebar {
		display : none;
	}

	.review__card:first-child {
		margin-top : 3em;
	}

	.review__card {
		flex-direction : row;
	}

	.review__card__contentBox {
		width : 60%;
	}

	.review__card__bookCover {
		width : 40%;
		padding : 1.5em 0em;
	}

	.review__card__bookCoverImage {
		width : 75%;
	}

	.review__heading {
		font-size : 1.5rem;
	}

	.review__textContainer {
		font-size : 1rem;
		margin-bottom : 1.6em;
	}

	.readMoreButton {
		padding: 1em 0em;
		font-size: 1rem;
	}
}

/* Single Column layout 401 ~ 500px */
@media (max-width : 500px) {
	.article__container {
		padding : 2em 4em;
	}

	.review__card {
		margin : 3.5em auto;
		flex-direction : column-reverse;
	}

	.review__card__contentBox {
		width : 100%;
	}

	.review__card__text {
		padding: 2em 2.5em;
	}

	.review__card__bookCover {
		width : 100%;
		padding : 1.5em 0em;
	}

	.review__card__bookCoverImage {
		width : 40%;
	}
}

/* One-column layout for smallest viewport */
@media (max-width : 400px) {

	.article__container {
		padding : 2em 3em;
	}

	.review__card {
		margin : 3.5em auto;
		flex-direction : column-reverse;
	}

	.review__card__bookCoverImage {
		width : 50%;
	}

	.topnav__sublist {
		display : none;
	}

}